import React from 'react';
import './App.css';
import EventListener from './EventListener';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      youtubeurl: "blb3X5zpdpU",
      time: 102,
      replayNum: 50,
      replayDuration: 10,
      caption: true,
      speed: 1,
      timerRemain: 0,
      timerRemainTime: 0,
      curtime: 0,
      videoDuration: 0,
      commandMode: true
    }
    this.onChangeUrl = this.onChangeUrl.bind(this);
    this.onChangeTime = this.onChangeTime.bind(this);
    this.onChangeReplayNum = this.onChangeReplayNum.bind(this);
    this.onChangeReplayDuration = this.onChangeReplayDuration.bind(this);

    this.onReplay = this.onReplay.bind(this);
    this.onReplay5 = this.onReplay5.bind(this);
    this.setTextInputRef = element => {
      this.textInput = element;
    };
    this.initialFocus = () => {
      // Focus the text input using the raw DOM API
      // if (this.textInput) this.textInput.focus();
      // this.textInput.blur();
      document.querySelector('#source').focus()
      window.scrollTo(0, 0);
    };
    this.timerList = []
    this.sourceText = null
    this.lastTransFocus = null
  }
  componentDidMount() {
    let urlParamStr = window.location.search
    if (urlParamStr) {
      //?を除去
      urlParamStr = urlParamStr.substring(1)

      let params = {}

      //urlパラメータをオブジェクトにまとめる
      urlParamStr.split('&').forEach(param => {
        const temp = param.split('=')
        //pramsオブジェクトにパラメータを追加
        params = {
          ...params,
          [temp[0]]: temp[1]
        }
      })
      if (params.reactyoutube) {
        let videoId = params.reactyoutube.replace(/.*%3D/g, '');
        this.setState({ youtubeurl: videoId })
      }
    }
    setInterval(() => {
      if (this.state.timerRemain > 0) {
        let remain = this.state.time + this.state.replayDuration - window['XXXXX'].getCurrentTime()
        let tremain = Math.trunc(remain * 10) / 10
        this.setState({ timerRemainTime: tremain })
      }
      //this.textFoucsCheck()
    }, 200);
    setInterval(() => {
      let el = document.activeElement
      if (el.id !== "source" && this.isPlaying()) {
        this.initialFocus()
      }
      if (window['XXXXXREADY']) {
        this.setState({
          curtime: window['XXXXX'].getCurrentTime(),
          videoDuration: window['XXXXX'].getDuration()
        })
      }
    }, 3000)
    let seekTime = localStorage.getItem('react-youtube-seekTime')
    if (seekTime) {
      this.setState({ time: parseFloat(seekTime) })
    }
  }
  componentWillUnmount() {
  }
  textFoucsCheck() {
    //3秒超えたらfocusを外す
    let el = document.activeElement
    if (el && el.id === "source") {
      if (!this.lastTransFocus) {
        this.lastTransFocus = new Date()
        this.sourceText = el.value
      } else if (this.sourceText !== el.value) {
        this.lastTransFocus = new Date()
        this.sourceText = el.value
      } else if (new Date().getTime() - this.lastTransFocus.getTime() > 5000) {
        this.initialFocus()
        this.lastTransFocus = null
        this.sourceText = null
      }
    } else {
      this.lastTransFocus = null
      this.sourceText = null
    }
  }
  onChangeUrl(e) {
    const newLocal = e.target.value;
    const replace = newLocal.replace(/.*=/g, '');
    this.setState({ youtubeurl: replace, time: 0 }, () => {
      window['XXXXX'].loadVideoById(this.state.youtubeurl, this.state.time);
      this.initialFocus()
    })
  }
  onChangeTime(e) {
    this.setTimeState(parseFloat(e.target.value))
  }

  setTimeState(sec) {
    this.setState({ time: sec });
    localStorage.setItem('react-youtube-seekTime', sec)
  }
  onChangeReplayNum(e) {
    this.setState({ replayNum: parseInt(e.target.value) });
  }
  onChangeReplayDuration(e) {
    this.setState({ replayDuration: parseFloat(e.target.value) });
  }

  onReplay(e) {
    window['XXXXX'].seekTo(this.state.time, true);
    // if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
    //   console.log("playvideo");
    //   window['XXXXX'].playVideo();
    // }
  }
  onReplay5(e) {
    this.clearAllTimer()
    if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
      this.setTimeState(window['XXXXX'].getCurrentTime());
      console.log("playvideo");
      window['XXXXX'].playVideo();
    } else {
      this.onReplay()
    }
    this.setState({ timerRemain: this.state.replayNum - 1 })
    this.onReplay5SetInterval(this.state.replayNum - 1);
  }
  onReplay5SetInterval(remainNum) {
    let num = 0;
    this.timerList.push(
      setInterval(() => {
        if (this.timerList.size === 0) {
          //キャンセルされたので何もしない
          return
        }
        console.log("onReplay5SetInterval")
        const newLocal = this.state.time + this.state.replayDuration;
        if (window['XXXXX'].getCurrentTime() > newLocal) {
          console.log("clearInterval")
          this.clearAllTimer()
          this.setState({ timerRemain: remainNum })
          if (remainNum > 0) {
            this.onReplay();
            this.onReplay5SetInterval(remainNum - 1);
          }
        }
        //0.2*2000回=400秒間タイマーが続いたら削除
        if (num++ > 2000) {
          console.log("clearInterval exceed")
          this.clearAllTimer()
        }
      }, 200)
    )
  }
  clearAllTimer() {
    this.timerList.concat().forEach(t => {
      clearInterval(t)
    })
    this.timerList = []
  }
  typeOf(obj) {
    return toString.call(obj).slice(8, -1).toLowerCase();
  }
  keyAnyMode(key, event) {
    switch (key) {
      case "Escape":
        this.setState({ commandMode: true })
        break;
      case "Enter":
        this.setState({ commandMode: true })
        this.initialFocus();
        if (this.isPause()) {
          window['XXXXX'].playVideo();
        }
        break;
      default:
        return false
    }
    return true;
  }
  keyInputMode(key, event) {
    switch (key) {
      case " ":
        let srcValue = document.querySelector('#source').value
        if (!srcValue || srcValue.match(/ $/)) {
          console.log(srcValue)
          this.setState({ commandMode: true })
        } else {
          return false
        }
        break;
      case "i":
        if (event.target.id !== "source") {
          document.querySelector('#source').value = ""
          this.initialFocus()
          this.setState({ commandMode: false })
          if (this.isPlaying()) {
            window['XXXXX'].pauseVideo();
          }
        } else {
          return false;
        }
        break;
      case "u":
        if (event.target.id !== "source") {
          this.initialFocus()
          this.setState({ commandMode: false })
          if (this.isPlaying()) {
            window['XXXXX'].pauseVideo();
          }
        } else {
          return false;
        }
        break;
      default:
        return false
    }
    return true;
  }
  keyCommandMode(key, event) {
    let url
    if (event.ctrlKey || event.metaKey) {
      return false
    }
    switch (key) {
      case "k":
        console.log("state:" + window['XXXXX'].getPlayerState())
        //https://developers.google.com/youtube/iframe_api_reference#Playback_status
        if (this.isPause()) {
          console.log("playvideo");
          window['XXXXX'].playVideo();
        }
        if (this.isPlaying()) {
          window['XXXXX'].pauseVideo();
          this.clearAllTimer()
          this.setState({ timerRemain: 0, timerRemainTime: 0 })
        }
        break;
      case "c":
        this.setState({ caption: !this.state.caption });
        break;
      case "s":
        if (this.state.speed === 1) {
          window['XXXXX'].setPlaybackRate(0.25)
          this.setState({ speed: 0.25 });
        } else {
          window['XXXXX'].setPlaybackRate(1)
          this.setState({ speed: 1 });
        }
        break;
      case "r":
        this.clearAllTimer()
        this.setState({ timerRemain: 0, timerRemainTime: 0 })
        this.onReplay();
        break;
      case "l":
        this.clearAllTimer()
        this.onReplay5();
        break;
      case "t":
        this.setTimeState(window['XXXXX'].getCurrentTime());
        break;
      case "h":
        if (this.isPlaying()) {
          window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 4), true);
        } else {
          window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 2), true);
        }
        break;
      case "j":
      case "ArrowLeft":
        if (this.isPlaying()) {
          window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 2), true);
        } else {
          window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 0.5), true);
        }
        break;
      case "i":
        document.querySelector('#source').value = ""
        this.initialFocus()
        this.setState({ commandMode: false })
        if (this.isPlaying()) {
          window['XXXXX'].pauseVideo();
        }
        break;
      case "u":
        this.initialFocus()
        this.setState({ commandMode: false })
        if (this.isPlaying()) {
          window['XXXXX'].pauseVideo();
        }
        break;
      case "p":
        if (this.isPlaying()) {
          window['XXXXX'].pauseVideo();
        }
        url = 'https://ejje.weblio.jp/content/' + encodeURIComponent(document.querySelector('#source').value)
        window.open(url, '_blank')
        break;
      case "g":
        if (this.isPlaying()) {
          window['XXXXX'].pauseVideo();
        }
        url = 'https://www.google.co.jp/search?&q=' + encodeURIComponent(document.querySelector('#source').value)
        window.open(url, '_blank')
        break;
      case "a":
        url = 'https://script.google.com/macros/s/AKfycbzMcO9wBPgl11qirD0Xit6zYUW56Quy91MhAPzapUkBQcSoWZe8/exec?word=' +
          encodeURIComponent(document.querySelector('#source').value.replace(/^ +/g, '')) + "&trans=" +
          encodeURIComponent(document.querySelector(".translation").textContent);
        window.open(url, '_blank')
        break;
      default:
        return false
    }
    return true;
  }
  keyProc(key, event) {
    console.log(key + " pressed")
    let done = false
    if (this.state.commandMode) {
      done = this.keyCommandMode(key, event);
    } else {
      done = this.keyInputMode(key, event);
    }
    if (!done) {
      done = this.keyAnyMode(key, event);
    }
    if (done) {
      console.log("keyProc done")
      event.stopPropagation()
      event.preventDefault();
    }
  }
  secToString(sec) {
    let intSec = Math.round(sec)
    let min = Math.floor(intSec / 60)

    return "" + this.zeroPadding(min, 2) + ":" + this.zeroPadding(intSec - min * 60, 2)
  }
  zeroPadding(num, length) {
    return ('0000000000' + num).slice(-length);
  }
  isPlaying() {
    if ([1].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
      return true
    }
    else {
      return false
    }
  }
  isPause() {
    if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
      return true
    }
    else {
      return false
    }
  }
  render() {
    return (
      <div className="App">
        <EventListener
          eventTarget={document}
          keydown={(event) => this.keyProc(event.key, event)}
        />
        <div id="hidecap" style={{
          position: "absolute", top: "390px", left: "0px", backgroundColor: "#CCCCFF",
          width: "700px",
          height: "120px",
          display: (this.state.caption ? "none" : "")
        }}></div>
        <div style={{
          display: "grid",
          gridTemplateColumns: "350px 100px 80px 80px"
        }}>
          <div >
            <input type="text" name="youtubeurl" style={{ width: "80px" }} size="20" ref={this.setTextInputRef} value={this.state.youtubeurl} onChange={this.onChangeUrl}></input>
            <input type="number" name="time" style={{ width: "80px" }} size="20" value={this.state.time} onChange={this.onChangeTime}></input>
            <input type="number" name="replayNum" style={{ width: "50px" }} size="20" value={this.state.replayNum} onChange={this.onChangeReplayNum}></input>
            <input type="number" name="replayDuration" style={{ width: "50px" }} size="20" value={this.state.replayDuration} onChange={this.onChangeReplayDuration}></input>
            <label >{this.state.commandMode ? "c" : "i"}</label>
            {/* <button type="button" onClick={this.onReplay}>replay</button> */}
            {/* <button type="button" onClick={this.onReplay5}>replayNum</button> */}
          </div>
          <label style={{ display: "block", margin: "auto" }}>{this.secToString(this.state.curtime) + "/" + this.secToString(this.state.videoDuration)}</label>
          <label style={{ display: "block" }}>{"timer残" + this.state.timerRemain}</label>
          <label style={{ display: "block" }}>{"時間" + ((x) => {
            if (x > 1) {
              return Math.trunc(x)
            }
            if (Math.trunc(x) === x) {
              return "" + x + ".0";
            } else {
              return x;
            }
          })(this.state.timerRemainTime)
          }</label>
        </div>
      </div >
    )
  }
}

export default App;
